.section {
    min-width: 720px;
    min-height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 120px auto;
}

.number {
    margin: 0;
    min-height: 120px;
    text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25),
    0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
}

.image {
    width: 120px;
    height: 120px;
}

.name {
    max-width: 500px;
    text-align: center;
}