.content {
    align-items: center;
    max-width: 600px;
}

.section {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    max-height: 656px;
    width: 550px;
}

.section::-webkit-scrollbar {
    width: 8px;
    background-color: #2f2f37;
}

.section::-webkit-scrollbar-thumb {
    scroll-behavior: smooth;
    background: #8585ad;
    border: 3px solid #8585ad;
}

.section::-webkit-scrollbar-thumb:hover {
    background: #8585ad;
}

.mainItem {
    display: flex;
    align-items: center;
    min-width: 536px;
}

.info {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    align-self: flex-end;
}

.price {
    display: flex;
    align-items: center;
}