.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 272px;
    cursor: pointer;
    position: relative;
}

.price {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}

.name {
    display: flex;
    justify-content: center;
    text-align: center;
}